.button {
  font-size: 20px;
  font-weight: 500;
  width: 100%;
  margin: 10px 10px 10px 0px;
  padding: 12px 4px;
  font-family: sans-serif;
  cursor: pointer;
  text-transform: uppercase;
  color: white;
  text-align: center;
  background: rgb(56, 56, 56);
  display: block;
  border: 0;
  outline: none;
}

.button:hover {
  background: rgb(151, 151, 151);
  color: black;
}
